.App {
  text-align: center;
}

.App-logo {
  /*animates logo, reference keyframes*/
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
  border-radius: 50%;
}

.App-header {
  background-color: pink;
  height: 200px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-weight: bold;
}

.grid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 20px;
}

.tile {
  width: 10em;
  padding: 1em;
  height: 10em;
  overflow: hidden;
  border-radius: 10%;
}

.tile-img {
  width: 100%;
  height: 100%;
  border-radius: 10%;
}

/*image in tile expands on hover*/
.tile:hover img {
  -webkit-transform: scale(1.25);
      -ms-transform: scale(1.25);
          transform: scale(1.25);
}

/*animates logo*/
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



